  margin-bottom: 30px;
  padding: 0 40px;
  text-align: start;
}
.blog-one__item .blog__blockquote-author::before {
  margin-right: auto;
  margin-left: 0;
}
.blog-one__tags {
  margin-bottom: 40px;
}
.blog-one__tags-title {
  margin-bottom: 15px;
}
.blog-one__links {
  display: flex;
  justify-content: space-between;
  padding: 40px 0 32px;
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
}
.blog-one__link {
  display: block;
  width: 276px;
  height: auto;
  position: relative;
  color: #707070;
  font-size: 14px;
  font-weight: 600;
}
.blog-one__link::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 23px;
}
.blog-one__link-prev {
  padding-left: 44px;
}
.blog-one__link-prev::before {
  left: 0;
  background-image: url(../images/icons/prev-link.svg);
}
.blog-one__link-next {
  padding-right: 44px;
  text-align: right;
}
.blog-one__link-next::before {
  right: 0;
  background-image: url(../images/icons/next-link.svg);
}
.blog-one__comments {
  margin-top: 35px;
}
.blog-one__comments-title {
  color: #363838;
  line-height: 32px;
  font-size: 20px;
  margin-bottom: 15px;
}
.blog-one__comments-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
}
.blog-one .comment__avatar {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 13px;
}
.blog-one .comment__reply {
  background-color: transparent;
  margin-left: 5px;
  border: none;
  padding: 0;
  font-size: 14px;
  font-weight: 600;
  color: #707070;
  cursor: pointer;
}
.blog-one .comment__author {
  color: #363838;
  line-height: 23px;
}
.blog-one .comment__date {
  margin-bottom: 10px;
  font-size: 14px;
  color: #a1a1a1;
}
.blog-one .form {
  display: flex;
  flex-direction: column;
}
.blog-one .form-title {
  margin-bottom: 25px;
  color: #363838;
  line-height: 23px;
}
.blog-one .form-group {
  display: flex;
  justify-content: space-between;
}
.blog-one .form-input,
.blog-one .form-message,
.blog-one .form-btn {
  border: 1px solid #dfdfdf;
  border-radius: 20px;
  padding: 6px 16px;
  outline: none;
}
.blog-one .form-input {
  width: 48%;
  margin-bottom: 20px;
}
.blog-one .form-input::placeholder {
  color: #a1a1a1;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.68rem;
}
.blog-one .form-message {
  width: 100%;
  margin-bottom: 20px;
  padding: 14px 17px;
  border-radius: 24px;
  min-height: 125px;
  resize: none;
}
.blog-one .form-message::placeholder {
  color: #a1a1a1;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.68rem;
}
.blog-one .form-btn {
  border: none;
  background-color: #363838;
  color: #fffff0;
  font-weight: 600;
  border-radius: 21px;
  padding: 6px 35px;
  width: 156px;
  cursor: pointer;
}
.blog-one .form-checkbox__lable {
  width: 100%;
  margin-bottom: 20px;
  font-size: 14px;
  color: #a1a1a1;
}

.blog-sidebar {
  margin-left: 30px;
  width: 350px;
}
.blog-sidebar__search {
  position: relative;
}
.blog-sidebar__search-input {
  padding: 6px 38px 6px 18px;
  border: 1px solid rgba(54, 56, 56, 0.7);
