  width: 100%;
  margin-bottom: 44px;
}
.blog-sidebar__search-input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 27px;
  color: #a1a1a1;
}
.blog-sidebar__search-btn {
