.collection {
  margin: 0 auto 105px;
}
.collection__title {
  color: #363838;
  font-size: 35px;
  line-height: 51px;
  padding-bottom: 25px;
  margin-bottom: 25px;
  position: relative;
  text-align: center;
}
.collection__title::after {
  position: absolute;
  content: "";
  width: 250px;
  height: 1px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  background-color: #6d7278;
}
.collection__text {
  max-width: 730px;
  margin: 0 auto 30px;
}
.collection__items {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 30px;
}
.collection__item {
  position: relative;
}
.collection__img {
  min-height: 350px;
  object-fit: cover;
}
.collection__info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all ease 0.7s;
}
.collection__info:hover {
  opacity: 1;
  transition: opacity ease 0.7s;
}
.collection__info-title {
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  color: #363838;
  padding-bottom: 19px;
  margin-bottom: 19px;
  position: relative;
}
.collection__info-title::after {
  content: "";
  position: absolute;
  width: 80%;
  height: 1px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  background-color: #6d7278;
}
.collection__info-text {
  font-weight: 600;
  font-size: 14px;
  color: #707070;
}

.map {
